import { useEffect, useState } from 'react';
import web3 from 'web3';
import { Web3ReactProvider, useWeb3React } from '@web3-react/core';
import { Web3Provider } from '@ethersproject/providers';
import { Link } from 'react-router-dom';
import { ethers } from 'ethers';
import {
  Button,
  Form,
  Grid,
  Icon,
  Header,
  Image,
  Input,
  Message,
  Segment,
  Dimmer,
  Loader,
  // Ref,
} from 'semantic-ui-react';
import { FormProvider, useForm, Controller } from 'react-hook-form';
import { IsConnectedWallet } from '../utils';
import { useCreateContract } from '../hooks';
import BallotContract from '../contract_build/Ballot.json';
// import BallotSol from '../../contracts/Ballot.sol';
import CounterContract from '../contract_build/Ballot.json';
// import { ethers } from 'hardhat';

/* function useEvent(name, func) {
  useEffect(() => {
    window.addEventListener(name, func);

    return function cleanup() {
      window.removeEventListener(name, func);
    };
  }, []);
} */

let indexAnswers = 1;

export default function NewBallotForm() {
  const [answers, setAnswers] = useState(['answer1']);
  const [newBallotContractAddress, setNewBallotContractAddress] = useState();
  const [loading, setLoading] = useState(false);
  const [deployerBallotContract, signer] = useCreateContract(BallotContract);
  // const [deployerBallotContract, signer] = useCreateContract(CounterContract);
  const methods = useForm();
  const { handleSubmit, control, register } = methods;

  // console.log('signer', signer);

  const onSubmit = async (formData) => {
    setLoading(true);
    console.log('formData', formData);

    const question = ethers.utils.formatBytes32String(formData.question);
    const answers = Object.keys(formData).reduce((prev, curr, currIndex) => {
      if (curr !== 'question') {
        console.log('prev', prev);
        return [...prev, ethers.utils.formatBytes32String(formData[curr])];
      }

      return prev;
    }, []);

    // return null;
    /*
    0x33428EDD7e7802a1d5A19c113660D8f44FDD99D2
    */
    // const newBallotContract = await ethers.getContractFactory('Ballot');
    const address = await signer.getAddress();
    console.log('signer.getAddress()', address);
    // console.log('web3.utils.fromAscii', web3.utils.fromAscii('test1'));
    // https://docs.ethers.io/v5/single-page/#/v5/api/contract/contract-factory/
    /* const contract = await deployerBallotContract.deploy([
      ethers.utils.formatBytes32String('Rama11')
    ]); */
    const contract = await deployerBallotContract.deploy(question, answers);
    console.log('contract', contract);
    try {
      console.log('contract address', contract.address);
      // console.log(contract.deployTransaction.hash);
      // const trans = contract.deployTransaction.wait();
      // console.log('trans', trans);
      const d = await contract.deployed();
      setLoading(false);
      setNewBallotContractAddress(contract.address);
      console.log('contract deployed', d);
      // console.log('contract.value()', contract.value());
    } catch (e) {
      console.log('error deploying contract', e);
    }
  };

  const handleAddAnswer = () => {
    setAnswers([...answers, `answer${++indexAnswers}`]);
  };

  const handleDeleteProposal = deletedAnswer => {
    setAnswers([...answers.filter(a => a !== deletedAnswer)]);
  }

  if (!IsConnectedWallet()) {
    return null;
  }

  return (
    <>
      <Dimmer active={loading}>
        <Loader />
      </Dimmer>
      <FormProvider {...methods}>
        <Form onSubmit={handleSubmit(onSubmit)} widths="equal">
          <>
            <Form.Field>
              <Input
                fluid
                placeholder="Why is the sky blue?"
                name="question"
                // label="Question"
                input={{ ref: register({ required: true }) }}
                // action="Add"
                label={
                  <Button onClick={handleAddAnswer}>
                    <Button.Content visible>Add answer option</Button.Content>
                  </Button>
                }
                labelPosition="right"
              />
            </Form.Field>
            <hr />
            {answers.map((a) => (
              <Form.Field>
                <Input
                  placeholder="..."
                  name={a}
                  // input={{ ref: register }}
                  input={{ ref: register({ required: true }) }}
                  label={
                    <Button onClick={() => handleDeleteProposal(a)}>
                      <Button.Content hidden>
                        <Icon name="delete" />
                      </Button.Content>
                    </Button>
                  }
                  labelPosition="right"
                />
              </Form.Field>
            ))}
          </>
          <Form.Field>
            <Button type="submit">Create</Button>
          </Form.Field>
          <Form.Field>
            <Link to="/" className="ui gray small button">
              Cancel
            </Link>
          </Form.Field>
          {newBallotContractAddress && (
            <p>
              You ballot address:{' '}
              <Link to={`/${newBallotContractAddress}`}/*  target="_blank" */>
                {newBallotContractAddress}
              </Link>
            </p>
          )}
        </Form>
      </FormProvider>
    </>
  );
}
